<template>
  <div class="rounded-lg bg-white p-2">
    <div v-if="!$store.getters.isLogin">
      <div>타일을 만들 수 있고</div>
      <div>(제작중) 내 그림에 타일을 불일 수 있습니다.</div>
      <div>(제작중) 내 그림에 다른 유저의 타일도 붙일 수 있습니다.</div>
      <div>
        현재는 guest 계정을 이용중입니다. <br />
        데이터가 삭제되거나 변경될 수 있으니 저장이 필요하시면 가입을 해주세요
      </div>
    </div>

    <div v-if="loadingPicture">
      <div class="py-2">내가 찍은 도트</div>

      <div class="flex justify-between p-1">
        <div class="w-16">번호</div>
        <div class="w-48">이름</div>
        <div class="w-12">사진</div>
        <div class="w-32">기능</div>
      </div>
      <div
        v-for="data in $store.state.listPicture"
        :key="data.rid"
        class="flex justify-between p-1"
      >
        <div class="w-16 px-2 py-1">{{ data.rid }}</div>
        <div class="w-48 px-2 py-1">
          {{ data.pic_name }}
        </div>
        <div class="w-12 px-2 py-1">
          <img
            v-if="data.pic_url.length > 0"
            :src="data.pic_url"
            class="w-full h-full"
          />
        </div>
        <div class="flex w-32 justify-end m-1">
          <button
            class="px-2 py-1 bg-indigo-600 text-white"
            @click="onEdit(data.rid)"
          >
            수정
          </button>
          <button
            class="px-2 py-1 ml-1 bg-red-600 text-white"
            @click="onDelete(data.uid)"
          >
            X
          </button>
        </div>
      </div>
      <div class="flex justify-center p-1 m-1">
        <div class="p-1">이름:</div>
        <input v-model="inputPictureName" class="p-1 border-2 border-black" />
        <button class="px-2 py-1 bg-indigo-600 text-white" @click="onAdd()">
          추가
        </button>
      </div>
    </div>
    <div class="text-red-600">
      {{ errorMessage }}
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      loading: false,
      errorMessage: "",
      loadingPicture: false, // true : 로그되었다.
      inputPictureName: "",
      loadingAddName: false,
      loadingDelete: false,
    };
  },
  async mounted() {
    console.log("Home mounted");

    if (this.$store.state.listPicture === null) {
      // 리스트가 없으면 불러온다.
      const reqData = {
        api: "getPictureList",
        loginToken: localStorage.getItem("loginToken"),
        userUid: this.$store.getters.userUid,
      };
      // console.log("getPictureList req", reqData);

      this.loading = true;
      const res = await this.$store.callAsync(reqData);
      this.loading = false;

      if (res.code != 100) {
        console.log("getPictureList fail", res);
        this.errorMessage = res.message;
        return;
      }

      console.log("getPictureList ok", reqData, res);
      this.$store.commit("setListPicture", res.listPicture);
      this.loadingPicture = true;
    } else {
      this.loadingPicture = true;
    }
  },
  methods: {
    async onAdd() {
      if (this.loadingAddName) {
        return;
      }

      const reqData = {
        api: "addPicture",
        loginToken: localStorage.getItem("loginToken"),
        userUid: this.$store.getters.userUid,
        inputName: this.inputPictureName,
      };

      this.loadingAddName = true;
      const res = await this.$store.callAsync(reqData);
      this.loadingAddName = false;

      if (res.code != 100) {
        this.errorMessage = res.message;
        return;
      }

      this.$store.commit("addPicture", res.newPicture);
      this.inputPictureName = "";
    },
    async onEdit(picUid) {
      console.log("onEdit", picUid);

      let pic = null;
      for (let i = 0; i < this.$store.state.listPicture.length; i++) {
        const data = this.$store.state.listPicture[i];
        if (data.rid !== picUid) {
          continue;
        }
        pic = data;
        break;
      }

      if (pic === null) {
        console.log("not found pic", picUid);
        return;
      }

      this.$router.push(`/pic/edit/${pic.rid}`);
    },
    async onDelete(picUid) {
      console.log("onDelete", picUid);

      const reqData = {
        api: "delPicture",
        loginToken: localStorage.getItem("loginToken"),
        userUid: this.$store.getters.userUid,
        picUid: picUid,
      };

      console.log("delPicture req", reqData);

      this.loadingAddName = true;
      const res = await this.$store.callAsync(reqData);
      this.loadingAddName = false;

      console.log("delPicture res", res);

      if (res.code != 100) {
        this.errorMessage = res.message;
        return;
      }

      this.$store.commit("delPicture", picUid);
      this.inputPictureName = "";
    },
  },
};
</script>
